#root{
  height: 100%;
}
html{
  height: 100%;
}
body{
  background: #e2e1e0;
}
.App {
  text-align: center;
}
.MuiButton-text {
    padding: 0px;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
 .mainDialoge  .rdw-editor-main {
  overflow-y: hidden;
}
.rdw-editor-main {
  height: 100%;
  overflow: auto;
  box-sizing: border-box;
  display: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.selection_menu_expanded_paper {
  width: 125px;
  margin-left: 18px;
}

.popper_paper
{
  width: 185px;
  margin-left: 18px;
  padding-left: 5px;
}
.popper_paper:after {
  content: " ";
  border-top: 15px solid transparent;
  border-right: 18px solid #ffffff;
  border-bottom: 17px solid transparent;
  position: absolute;
  bottom: 120px;
  left: 4px;
  margin-left: auto;
}
.popper_paper svg, .smart_draw_paper svg{
  padding: 5px;
  cursor: pointer;
}
.smart_draw_paper{
  width: 12rem;
  margin: 18px;
  margin-top: 4rem;
}
.smart_draw_paper:after {
  content: " ";
  border-top: 15px solid transparent;
  border-right: 18px solid #ffffff;
  border-bottom: 17px solid transparent;
  position: absolute;
  top: 12rem;
  left: 4px;
  margin-left: auto;
}

.container {
  width:100%;
  height: 100%;
  top: 0;
  left:0;
}
.header-area {
  position: absolute;
  top: 10px;
  left: calc(50% - 250px);
  width: 500px;
  height: 50px;
  display: flex;
}

.top-left-menu {
  position: absolute;
  width: 85px;
  height: 50px;
}

.help-link {
  text-decoration: none;
  color:#0000EE;
}

.top-right {
  position: absolute;
  top: 10px;
  right: 15px;
  display: block;
}

.footer-area {
  position: absolute;
  bottom: 10px;
  left: calc(50% - 175px);
  width: 350px;
  height: 50px;
  display: flex;
}

.undo-redo-buttons {
  display: flex;
  margin-right: 20px;;
  background-color: white;
  filter: drop-shadow(0 1px 2px rgba(0, 0, 0, .3));
}

.zoom-info {
  display: flex;
  background-color: white;
  filter: drop-shadow(0 1px 2px rgba(0, 0, 0, .3));
}

.layover {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(200, 200, 200, 0.4);
  display: flex;
  justify-content: center;
  z-index: 1500;
}

.layover-col {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.layover-box {
  display: flex;
  flex-direction: column;
  flex-basis: auto;
  background-color: white;
  padding: 20px;
  filter: drop-shadow(0 1px 2px rgba(0, 0, 0, .3));
}
.layover-box a {
  text-decoration: none;
  color:#0000EE;
  margin-top: 10px;
}

.bodyClass{
  height: 100%;
  width: 100%;
}
.header_text svg{
  font-size: 10px;
  position: relative;
  top: 4px;
  right:2px;
}
.header_text >p{
  background: #f5f5f5;
  padding: 10px;
}
.HeaderSection >div{
  padding: 0 18px;
}
#root .HeaderMenuIcon{
  width: 50px;
  background: #fff;
  border-radius: 5px;
  padding: 5px 0;
  filter: drop-shadow(0 1px 2px rgba(0, 0, 0, .3));
  color: #474747;
}
.HeaderMenuIcon svg{
  font-size: 32px;
}

.side-bar{
  top: 70px;
  width: 85px;
  position: absolute;
  left: 0px;
}

.side_menus>div:hover, .popper_paper svg:hover, .smart_draw_paper svg:hover
{
  background-color:#f2f2ff;
}
.footer{
  bottom: 0;
  position: fixed;
  background-color: rgba(200, 200, 200, 0.2);
}
.sideBarMenu.active{
  background-color: #dedefc;
  border-left: 4px solid #2f3296;
}

.MuiMenu-paper{
  top: 16px !important;
}

.subIcon svg{
  margin-left: 4em;
}

.text-editor{
    margin: 0 15px;
    filter: drop-shadow(0 1px 2px rgba(0, 0, 0, .3));
    display: flex;
    justify-content: center;
    width: 100%;
}
.canvasClass{
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.canvasGridClass{
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: white;
}

.viewport {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.canvasContainer {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.dialogclose{
  float: right;
  bottom: 14px;
    left: 2rem;
}
.header{
  position: relative;
  height: 60px;
  background-color: rgba(200, 200, 200, 0.2);
}

.changeBorder svg{
  background-color: #c7c7c7;
}

.changeBorderRound svg {
  border: 1px solid blue;
  border-radius: 20px;
}
.defaultBorder svg{
  border: 1px solid transparent;
  border-radius: 20px;
}

@media only screen and (max-width: 640px) {
  .header{
    margin: 4px 0;
  }
  .side_menus {
    padding: 0px !important;
    position: absolute !important;
    bottom: 0;
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    max-width: 100% !important;
    left: 0 !important;
    margin: 0 !important;
  }

  .side-bar {
    width: 100%;
    height: 45px;
    top: unset;
    bottom: 2px;
  }

  .side-bar .sideBarMenu{
    padding: 12px 10px;
  }

  .sideBarMenu.active{
    border-bottom: 4px solid #2f3296;
    border-left:none
  }

  .header_text >p{
    background-color: none;
    padding: 0;
  }
  .footer{
    top:12px;
    bottom: unset !important;
    margin: 3px;
  }
  .footer-area {
    top: 12px;
    left: calc(50% - 50px);
    width: 100px;
  }
  .zoom-info {
    display: none;
  }
  .header-area {
    display: none;
  }

  #root .HeaderMenuIcon{
    margin:0 !important;
  }

  .counter_percent, .header_text p span{
    display: none !important;
  }

  #root .header_text{
    margin: 0;
    border:none;
  }
  .header_line, .header_text p a{
    display: block !important;
    float: right;
  }

  #root .undo_redo_section{
    margin: 0px;
  }
 
  .smart_draw_res svg{
    padding: 5px;
  }

  .smart_draw_res:after{
    content: " ";
    border-left: 20px solid transparent;
    border-right: 20px solid transparent;
    border-top: 20px solid #fff;
    bottom: 22px;
    left: 10rem;
    margin-left: auto;
  }
   .side-bar .side_menus{
    width: 100%;
    display: inline-flex;
    height: inherit;
  }
  .side-bar .menuItems{
    display:none;
  }
  #root .resButton{
    padding: 1px 8px;
  }
  .smart_draw_paper {
    width: 100%;
    margin: 0;
    left: 5px;
    bottom: 0;
  }
  .smart_draw_res{
    width: 100%;
    bottom: 0px;
    position: relative;
  }
  .poper_paper_res{
    width: calc(100% - 9px);
    bottom: 0px;
    position: relative;
    margin: 0px;
  }
  .poper_paper_res svg{
    padding: 4px;
  }
  .popper_paper:after, .smart_draw_res:after{
    display: none;
  }
}
